@import "~normalize.css";
@import "src/styles/fonts.module.scss";
@import "src/styles/colors.module.scss";
@import "src/styles/sizes.module.scss";

body {
  margin: 0;
  font-family: 'Montserrat', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Roboto Mono', monospace;
}

hr {
  padding-top: 15px;
  padding-bottom: 14px;
  border: none;
  border-bottom: thin dashed lightgray;
}

strong {
  font-weight: 600;
}

h1 {
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  line-height: 59px;
  color: #232323;

  @media (max-width: $medium-size) {
    font-size: 40px;
    line-height: 49px;
  }
}

h2 {
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 44px;
  color: #121331;

  @media (max-width: $medium-size) {
    font-size: 30px;
    line-height: 30px;
  }
}

h3 {
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
  color: #232323;

  @media (max-width: $medium-size) {
    font-size: 20px;
    line-height: 24px;
  }
}
