@import "src/styles/colors.module";
@import "src/styles/sizes.module";

.Layout {

  &Side {
    z-index: 1000;
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    max-height: 100vh;
    background-color: $dark-gray-color;
    max-width: 320px;

    @media (min-width: $large-size) {
      max-width: 380px;
    }

    @media (max-width: $medium-size) {
      min-width: 100vw;
      max-height: 78px;
    }
  }

  &Body {
    padding-left: 360px;
    background-color: $white-color;
    max-height: 100vh;

    @media (max-width: $medium-size) {
      padding-top: 80px;
      padding-left: 0;
    }
  }
}
