@import "src/styles/colors.module";

.Menu {
  display: flex;
  flex-direction: column;
  gap: 25px;

  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: $light-font-color;
  list-style-type: none;

  &:only-child {
    padding-left: 0;
  }

  &Item {
    color: $light-font-color;

    &Text {
      display: flex;
      align-items: center;
      gap: 10px;
    }

    &Link {
      cursor: pointer;
      text-decoration: none;
      color: $light-font-color;

      &:hover {
        color: $active-font-color;
      }

      &:active{
        color: $active-font-color;
      }
    }
  }
}
