@import "src/styles/colors.module";
@import "src/styles/sizes.module";

.Sidebar {
  background-color: $dark-gray-color;
  padding: 50px;

  @media (min-width: $large-size) {
    padding: 80px;
  }

  @media (max-width: $medium-size) {
    padding: 10px 0 10px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
