@import "src/styles/colors.module";
@import "src/styles/sizes.module";

.Achievement {
  display: grid;
  gap: 10px;
  grid-template-columns: 100px 1fr;
  align-items: center;
  width: 350px;

  @media (max-width: $medium-size) {
    grid-template-columns: 50px 1fr;
    width: 200px;
  }

  &Icon {
    height: auto;
    width: 100%;
  }

  &Text {
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 30px;

    @media (max-width: $medium-size) {
      font-size: 15px;
      line-height: 20px;
    }
  }

}
