@import "src/styles/sizes.module";

.Block {
  &Header {
    display: flex;
    align-items: center;
    gap: 15px;

    @media (max-width: $medium-size) {
      gap: 5px;
    }
  }

  &Level1 {
    padding-left: 0;
  }

  &Level2 {
    padding-left: 20px;
    @media (max-width: $medium-size) {
      padding-left: 0;
    }
  }

  &Level3 {
    padding-left: 40px;
    @media (max-width: $medium-size) {
      padding-left: 0;
    }
  }
}
