@import "src/styles/colors.module";
@import "src/styles/sizes.module";

.Navigation {
  padding-top: 40px;
  padding-left: 0;

  @media (min-width: $large-size) {
    padding-top: 60px;
  }

  @media (max-width: $medium-size) {
    display: none;

    &Visible {
      padding: 15px 10vw;
      left: 0;
      top: 0;
      display: flex;
      width: 80vw;
    }
  }

  &Button {
    display: none;

    @media (max-width: $medium-size) {
      display: block;
      margin: 10px 20px;
    }
  }

}
