@import "src/styles/colors.module";
@import "src/styles/sizes.module";

.AboutPage {
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 30px;

  @media (max-width: $medium-size) {
    font-size: 15px;
    line-height: 25px;
  }

  & p:hover {
    background-color: #FAFAFA;
  }

  &Achievements {
    display: flex;
    flex-wrap: wrap;
    gap: 120px;
    justify-content: center;
    padding-top: 30px;

    @media (max-width: $medium-size) {
      gap: 50px;
    }
  }
}
